		},
		data(){
			return {
				age: year-1996
			}
		},
		methods: {

			// on:click close - hide MenuTab component
			close(event){
				let menuClass = event.currentTarget.parentElement.classList[1];
        let menuTab = document.getElementsByClassName('menuTab ' + menuClass)[0];
        let style = menuTab.style.display;
        if (style !== 'none')
					menuTab.style.display = "none";

			}

		}

	}

</script>

